<template>
  <div class="box column is-half is-offset-one-quarter">
    {{ data.ITEM_NAME }}
    <span v-if="type == 'nutrition'" class="m-1">
      <button class="is-small button is-link is-rounded is-light"
              @click="showModal">Info</button>
    </span>
    <Badge :type="type"/>
    <InfoModal v-if="type == 'nutrition'" 
               :activated="activated" 
               @hide-modal="hideModal"
               :type="type"
               :info="data"/>
  </div>
</template>

<script>
import Badge from './Badge.vue'
import InfoModal from './InfoModal.vue'

export default {
  name: "ResultItem",
  components: {
    Badge,
    InfoModal,
  },
  props: ["type", "data", "id"],
  data: function(){
    return {
      activated: false
    }
  },
  methods: {
    hideModal: function(){
      this.activated = false
    },
    showModal: function(){
      this.activated = true
    }
  }
}

</script>

<style></style>

